import React, { useContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useForm } from "react-hook-form";

import AuthApis from '../APIs/AuthApis';
import WebContext from '../../../application/contexts/WebContext';
// import ForgotPassword from '../ForgotPassword';
import Notifier from '../../../application/utils/Notifier';

import asset_login_background from './../../../assets/images/bg-chevron.png';
import assetLogo from './../../../assets/images/logo-nodis.png';
import { ReactComponent as AssetBackArrow } from './../../../assets/images/icons/back-arrow.svg';

import './login.scss';

const Login = () => {

    const { register, handleSubmit, formState: { errors } } = useForm();
    const webCtx = useContext(WebContext);
    const navigate = useNavigate();
    const [currentTheme, setCurrentTheme] = useState(webCtx.appTheme);
    const [isWaiting, setIsWaiting] = useState(false);
    const { t } = useTranslation();

    useEffect(() => {
    }, []);

    const switchTheme = () => {
        const previousTheme = webCtx.appTheme;
        const theme = (webCtx.appTheme === 'light-theme') ? 'dark-theme' : 'light-theme';
        document.body.classList.replace(previousTheme, theme);
        setCurrentTheme(theme);
        webCtx.switchTheme(theme);
    };

    const handleLogin = (data) => {
        setIsWaiting(true);
        const formData = {
            email: data.email,
            password: data.password
        }
        AuthApis.login(formData)
            .then(res => {
                setIsWaiting(false);

                localStorage.setItem('auth_token', res.data.access_token);
                localStorage.setItem('username', res.data.user_name);

                webCtx.changeLoginStatus(true);
                webCtx.setLoginUserName(res.data.user_name);

                console.log('access-type', res.data.access_type);

                if (res.data.access_type === 'admin_access') {
                    navigate("/admin-portal");
                } else {
                    navigate("/");
                }

            })
            .catch(err => {
                // console.log(err);
                setIsWaiting(false);
                Notifier.notify('error', "Email and/or Password is incorrect");
            });
    };

    return (
        <div className="login-container">
            <div className="login-wrapper">
                <div className="row no-gutters h-100">
                    <div className="col-lg-6 d-none d-lg-block pe-lg-0" >
                        <div className="login-banner-area" style={{ backgroundImage: `url(${asset_login_background})` }}>
                            <div className="header-section">
                                <img src={assetLogo} alt="Nodis-Logo" />
                                {/* <div>{t('imparo_technic')}</div> */}
                            </div>
                            <div className="fw-medium h2 text-center">{t('nodis_slogan')}</div>
                            <div className="footer-section">
                                <h3><span>{t('welcome_back')}</span></h3>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 ps-lg-0">
                        <div className="row h-100">
                            <div className="col-md-12">
                                <div className="d-flex justify-content-between px-3 pt-3">
                                    <div>
                                        <button className="back-btn" onClick={() => navigate('/')}>
                                            <span>
                                                <AssetBackArrow />
                                                {/* <img src={asset_back_arrow} alt="back" className={currentTheme === 'light-theme' ? "dark-icon" : "light-icon"} /> */}
                                            </span>
                                        </button>
                                    </div>
                                    {/* <div className="action-item">
                                        <button className="square-btn toggle-theme" onClick={switchTheme}>
                                            <span >
                                                <img src={currentTheme === 'light-theme' ? asset_dark : asset_bright} alt="theme-icon" />
                                            </span>
                                        </button>
                                    </div> */}
                                </div>
                                <div className="login-area" >
                                    <div className="row w-100">
                                        <div className="col-10 col-sm-8 mx-auto">
                                            <div className="fancy-heading mb-3 h3">{t('login_account')}</div>
                                            <div className="w-100">
                                                <form onSubmit={handleSubmit(handleLogin)}>
                                                    <div className="mb-3">
                                                        <input type="email" className="form-control imparo-input-field" placeholder={t('email_id')} name="email" {...register("email", { required: true })} />
                                                        {errors.email && <div className="text-end"><small className="text-danger">Email is required</small></div>}
                                                    </div>
                                                    <div className="mb-3">
                                                        <input type="password" className="form-control imparo-input-field" placeholder={t('password')} name="password" {...register("password", { required: true })} />
                                                        {errors.password && <div className="text-end"><small className="text-danger">Password is required</small></div>}
                                                    </div>
                                                    {/* <div className="mb-3 d-flex">
                                                        <Link to="#" className="forget-password ms-auto" data-bs-toggle="modal" data-bs-target="#forgotPasswordModal">{t('forget_password')}</Link>
                                                    </div> */}
                                                    <button type="submit" className="btn nodis-btn outline btn-block w-100" disabled={isWaiting}>
                                                        {isWaiting &&
                                                            <div className="button-loader">
                                                                {t('loading')}...
                                                            </div>
                                                        }
                                                        {t('login')}
                                                    </button>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* <ForgotPassword /> */}
        </div>
    )
}

export default Login
